@import '../../../assets/styles/variables.scss';
@import '../../common/table/Table.module.scss';

.title{
    display: flex;
    justify-content: center ;
    margin-bottom: 34px;
    text-transform: uppercase;
    font-family: $mainFont;
    font-size: 32px;
    font-weight: 700;
    line-height: 39px;
    color: $customBlue;
}

.emptyInput {
    border-color: $customRed;
    border-width: 2px;
}

@media screen and (max-width: 480px)  {
    .title {
        margin-top: 15px;
        margin-bottom: 15px;
        font-size: 18px;
    }

    .mainContainer {
        display: grid;
        row-gap: 10px;
        justify-content: center;
    }
}

@media screen and (min-width:481px) and (max-width:768px) {
    .mainContainer {
        display: grid;
        row-gap: 10px;
        justify-content: center;
    }
}

@media screen and (min-width:769px) and (max-width:1279px) {
    .mainContainer {
        display: grid;
        row-gap: 10px;
        justify-content: center;
    }
}

@media screen and (min-width:1280px) {
    .mainContainer {
        display: grid;
        row-gap: 13px;
        justify-content: center;
    }
}
