.links {
    text-decoration: none;
    color: black;
}

@media only screen and (min-width:0) and (max-width: 300px) {
    .headerMUI {
        width: 100%;
    }

    .myaccountMUI {
        display: none;
    }
}

@media only screen and (min-width: 301px) and (max-width: 900px) {
    .headerMUI {
        width: 100%;
    }

    .myaccountMUI {
        display: block;
    }
}